@keyframes typing {
  0% {
    width: 0;
  }

  to {
    width: 100%;
  }
}

@keyframes blink {
  0%, to {
    border-color: #0000;
  }

  50% {
    border-color: #4ade80;
  }
}

@keyframes float {
  0%, to {
    transform: translateY(0)rotate(0);
  }

  50% {
    transform: translateY(-20px)rotate(5deg);
  }
}

@keyframes float-reverse {
  0%, to {
    transform: translateY(0)rotate(0);
  }

  50% {
    transform: translateY(20px)rotate(-5deg);
  }
}

.animate-typing {
  white-space: nowrap;
  animation: 3.5s steps(40, end) forwards typing;
  overflow: hidden;
}

.animate-blink {
  animation: 1s step-end infinite blink;
}

.animate-float {
  animation: 8s ease-in-out infinite float;
}

.animate-float-reverse {
  animation: 8s ease-in-out infinite float-reverse;
}

